.slides{
    margin-top: 55px;
    text-align: center;
}

.images_slide{
    width: 100%;
    height: 10%;
    margin-left: auto;
    margin-right: auto;
}

.carousel-inner>.item>a>img, .carousel-inner>.item>img, .img-responsive, .thumbnail a>img, .thumbnail>img{
    height: 500px;
}

.carousel-control.left{
    background-image: -webkit-gradient(linear,left top, right top,color-stop(0, rgba(0, 0, 0, 0)),to(rgba(0, 0, 0, 0)));
    background-image: -webkit-linear-gradient(left,rgba(0, 0, 0, 0) 0,rgba(0, 0, 0, 0) 100%);
    background-image: -o-linear-gradient(left,rgba(0, 0, 0, 0) 0,rgba(0, 0, 0, 0) 100%);
    background-image: linear-gradient(to right,rgba(0, 0, 0, 0) 0,rgba(0, 0, 0, 0) 100%);
}

.carousel-control.right{
    background-image: -webkit-gradient(linear,right top, left top,color-stop(0, rgb(0, 0, 0, 0)),to(rgba(0, 0, 0, 0)));
    background-image: -webkit-linear-gradient(right,rgb(0, 0, 0, 0) 0,rgba(0, 0, 0, 0) 100%);
    background-image: -o-linear-gradient(right,rgb(0, 0, 0, 0) 0,rgba(0, 0, 0, 0) 100%);
    background-image: linear-gradient(to left,rgb(0, 0, 0, 0) 0,rgba(0, 0, 0, 0) 100%);
}


